
import {mapState,mapMutations} from 'vuex';
import XHeader from 'vux/src/components/x-header/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'

import userSearch from "./search.vue";
export default {
name:'user',
data () {
return {

}
},
computed:{
...mapState({
userList: state => state.common.user.userList,
roleList: state => state.common.user.roleList,
vm:state => state.common.user.vm
})
},
components: {
XHeader,
Popup,
PopupHeader,
Checklist,
userSearch
},
created () {
this.query();
},
methods:{
...mapMutations({
query:'common/user/getList',
getRoles:'common/user/getRoles',
handleAssignRole:'common/user/assignRole',
}),
gotoForm(item){
this.$store.commit('common/user/clear');
if(item){
this.$router.push({path:"/system/user/form",query:{userId:item.id}});
}else{
this.$router.push({path:"/system/user/form"});
}
},
handleDelete(userId) {
const _this = this;
this.$vux.confirm.show({
title:"删除提示",
content:"确定要删除该条记录?",
onCancel () {
_this.$vux.toast.text('已取消删除', 'top');
},
onConfirm () {
_this.$store.commit({
type: 'common/user/delete',
id: userId
})
}
})
}
}
}



