
import {mapState,mapMutations} from 'vuex';
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'

export default {
name:"user-search",
data () {
return {

}
},
components:{
Group,
XInput,
XButton,
Selector
},
created () {
this.getDepartmentList();
},
computed:{
...mapState({
vm:state => state.common.user.vm,
queryModel:state => state.common.user.queryModel
})
},
methods:{
...mapMutations({
getDepartmentList:'common/user/getDepartmentList',
getUserSearch:'common/user/getUserSearch'
}),
}
}



